<template>
    <div id="dashboard">
        <h3>Dashboard</h3>
        <ul class="collection with-header">
            <li class="collection-header">Questions</li>
            <li v-for="question in questions" v-bind:key="question.id" class="collection-item">
                {{question.content}}
                <router-link class="secondary-content" v-bind:to="{name: 'view-question', params: {question_id: question.question_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
        return {
            questions: []
        }
    },
    created () {
        db.collection('questions').orderBy('question_id').get()
            .then(querySnapshot => {
                querySnapshot.forEach(
                    doc => {
                        const data = {
                            'id': doc.id,
                            'question_id': doc.data().question_id,
                            'content': doc.data().content
                        }
                        this.questions.push(data)
                    }
                )
            })
    }
}
</script>


