<template>
    <div id="view-question">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{content}}</h4></li>
        </ul>
        <router-link to = "/" class="btn grey">Zpět</router-link>
        <button @click="deleteQuestion" class="btn red">Smazat</button>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-question',
    data () {
        return {
            question_id: null,
            content: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('questions').where('question_id', '==', to.params.question_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                        vm.question_id = doc.data().question_id
                        vm.content = doc.data().content
                    })
                })
        })
    },

    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('question').where('question_id', '==', this.$route.params.question_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.question_id = doc.data().question_id
                        this.content = doc.data().content
                    })
                })
        },
    deleteQuestion () {
        if(confirm('Smazat otázku?')) {
            db.collection('question').where('question_id', '==', this.$route.params.question_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
        }
    }    
    }
}
</script>


